import React, { Component } from 'react'
import styled, {keyframes} from 'styled-components'

const InputField = styled.input`
    text-align:center;
    margin-bottom:2em;
    width:30em;
`

const rotate = keyframes`
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
`


const slideIn = keyframes`
    from {
        right:0;
    }

    to {
        right:100vw;
    }`

const Flex = styled.div`
    border:1px black solid;
    background-color:${props => props.bgColor || "auto" };
    display:${props => props.display || "flex" };
    position: ${props => props.position || "auto" };
    flex-direction: ${props => props.direction || "column"};
    align-items: ${props => props.alignItems || "center"};
    justify-content: ${props => props.justify || "center"};
    width: ${props => props.width || "auto" };
    top: ${props => props.top || "0" };
    min-width: ${props => props.minWidth || "auto" };
    left: ${props => props.left || "auto" };
    right: ${props => props.right || "auto" };
`

const FlexSlider = styled.div`
    border:1px black solid;
    background-color:${props => props.bgColor || "auto" };
    display:${props => props.display || "flex" };
    position: ${props => props.position || "auto" };
    flex-direction: ${props => props.direction || "column"};
    align-items: ${props => props.alignItems || "center"};
    justify-content: ${props => props.justify || "center"};
    width: ${props => props.width || "auto" };
    top: ${props => props.top || "0" };
    min-width: ${props => props.minWidth || "auto" };
    left: ${props => props.left || "auto" };
    right: ${props => props.right || "auto" };
    overflow:hidden;
    animation: ${slideIn} 1s 2 alternate;
    z-index: ${props => props.right || "auto" };
`

const ColorBox = styled.span`
    display:flex;
    width:13px;
    height:13px;
    background-color:${props => props.color || "white"};
    border: 1px solid black;
    margin-left:5px;
    border-radius:3px;
    cursor:pointer;
    animation: ${rotate} 3s infinite alternate;

    &:hover {
        border: 3px solid black;
    }
`

export default class SouvenirForm extends Component {

    state={
        name:"",
        location:"",
        color:"",
        audio:"",
        pageTwo:true,
    }

    pickColor = (color) => {
        this.setState({
            color:color
        })
    }

    handleChange = (e) => {
        this.setState({
            [e.target.id]:e.target.value
        })
        
    }

    nextPage = () => {
        this.setState(prevState => ({
            pageTwo: !prevState.check
        }));
    }

  render() {
    return (
        <FlexSlider direction="row" minWidth="100%" position="relative" justify="row" animation="3s ${rotate} infinite linear">
            <Flex minWidth="100vw" position="relative">
                <InputField type="text" placeholder="Name" id="name" onChange={this.handleChange}/>
                <InputField type="text" placeholder="Location" id="location" onChange={this.handleChange}/>
                <InputField type="text" placeholder="Add audio link" id="audio" onChange={this.handleChange}/>
                <Flex direction="row" position="">
                    <ColorBox animation="${rotate} 3s infinite alternate" color="#DC143C" onClick={() => (this.pickColor("#DC143C"))}/>
                    <ColorBox color="#87CEFA" onClick={() => (this.pickColor("#87CEFA"))}/>
                    <ColorBox color="#1E90FF" onClick={() => (this.pickColor("#1E90FF"))}/>
                    <ColorBox color="#00FF7F" onClick={() => (this.pickColor("#00FF7F"))}/>
                    <ColorBox color="#FA8072" onClick={() => (this.pickColor("#FA8072"))}/>
                </Flex>
                <ColorBox color={this.state.color}/>
                <button onClick={this.nextPage}>Start writing</button>
                <h3>{this.state.name}</h3>
                <h3>{this.state.location}</h3>
                <h3>{this.state.audio}</h3>
            </Flex>
            <Flex position="fixed" left="100vw" display={ this.state.pageTwo ? "flex" : "none"} minWidth="100vw" bgColor="coral">Heya</Flex>
        </FlexSlider>
    )
  }
}